// frontend-react/src/components/FileUploader.jsx
import React, { useState } from "react";
import { uploadExcel, uploadCsv } from "../services/api";

export default function FileUploader({ onDataLoaded }) {
  const [file, setFile] = useState(null);
  const [fileType, setFileType] = useState("excel");
  const [sheet, setSheet] = useState(0);
  const [range, setRange] = useState("A1:C10");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  const handleUpload = async () => {
    if (!file) return;
    setLoading(true);
    setError(null);

    try {
      let response;
      if (fileType === "excel") {
        response = await uploadExcel(file, sheet, range);
      } else {
        response = await uploadCsv(file);
      }
      onDataLoaded(response.data);
    } catch (err) {
      setError(err.message || "Upload failed");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ marginBottom: 20 }}>
      <select
        value={fileType}
        onChange={(e) => setFileType(e.target.value)}
        style={{ marginRight: 10 }}
      >
        <option value="excel">Excel (.xlsx)</option>
        <option value="csv">CSV</option>
      </select>
      {fileType === "excel" && (
        <>
          <input
            type="number"
            value={sheet}
            min={0}
            onChange={(e) => setSheet(Number(e.target.value))}
            style={{ width: 50, marginRight: 10 }}
          />
          <input
            type="text"
            value={range}
            onChange={(e) => setRange(e.target.value)}
            placeholder="Range (e.g. A1:C10)"
            style={{ marginRight: 10 }}
          />
        </>
      )}
      <input type="file" onChange={handleFileChange} />
      <button onClick={handleUpload} disabled={loading}>
        {loading ? "Uploading..." : "Upload"}
      </button>
      {error && <div style={{ color: "red" }}>{error}</div>}
    </div>
  );
}
